	import V from '../../utils/validate'
	import {  
        mapMutations  
    } from 'vuex';

	export default{
		data(){
			return {
				mobile: '',
				password: '',
				logining: false
			}
		},
		onLoad(){
			
		},
		methods: {
			...mapMutations(['login']),
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack();
			},
			toRegist(){
				uni.navigateTo({
					url: '/pages/public/register'
				})
			},
			async toLogin(){
				// this.logining = true;
				const {mobile, password} = this;
				// 数据验证模块
				if(!V.tel(mobile)) {
					this.$api.msg('请输入正确手机号！');
					this.logining = false;
					return;
				} else if (!V.password(password)) {
					this.$api.msg('密码至少包含数字跟字母，可以有字符,长度6-20!');
					this.logining = false;
					return;
				}
				const data = await this.Api.login(mobile,password)
				if(data.code === 1){
					this.login(data.data);
					console.log(this.$api.prePage());
					if(this.$api.prePage() && this.$api.prePage().route !=='pages/public/login') {
						uni.navigateBack({
						    delta: 1
						});
					} else {
						uni.switchTab({
						    url: '/pages/index/index'
						});
					}					
				}else{
					this.logining = false;
				}	
			}
		},

	}
